export default {
  layout: "Emp-content",
  data() {
    return {
      name: "",
      amount: "",
      class_id: "",
      fund_id: "",
      sub_fund_id: "",
      classes: "",
      funds: "",
      subFunds: "",
      purposes: null,
      month_wise: "",
      error: "",
    };
  },
  created() {
    this.getClass();
    this.getFund();
    this.getPurpose();
  },
  computed: {
    totalFee() {
      if (this.admissionFee && this.monthlyFee) {
        return parseInt(this.admissionFee) + parseInt(this.monthlyFee);
      }
    },
  },
  methods: {
    createPurpose() {
      this.$axios
        .post("/accounts/purpose", {
          name: this.name,
          amount: this.amount,
          month_wise: this.month_wise,
          class_id: this.class_id,
          fund_id: this.fund_id,
          sub_fund_id: this.sub_fund_id,
        })
        .then((response) => {
          this.name = "";
          this.amount = "";
          this.month_wise = "";
          this.class_id = "";
          this.fund_id = "";
          this.sub_fund_id = "";
          this.getPurpose();
          this.$toaster.success(response.data.message);
        })
        .catch((error) => {
          this.error=''
          if (error.response.status === 422) {
            this.error = error.response.data.errors;
          }
          console.log(error);
        });
    },
    getClass() {
      this.$axios
        .get("/accounts/class")
        .then((response) => {
          this.classes = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getPurpose() {
      this.$axios
        .get("/accounts/purpose")
        .then((response) => {
          this.purposes = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getFund() {
      this.$axios
        .get("/accounts/funds")
        .then((response) => {
          this.funds = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getSubfund() {
      this.$axios
        .get("/accounts/funds-subfunds/" + this.fund_id)
        .then((response) => {
          this.subFunds = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
